import React, { useState, useEffect } from "react";
import { useApiOperation } from "../../hooks/useApiOperation";
import { Card, Button } from "../../components/common";
import { toast } from "react-hot-toast";
import styles from "./UserManagementPage.module.css";

const UserManagementPage = () => {
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [selectedUser, setSelectedUser] = useState(null);
  const [showStatusModal, setShowStatusModal] = useState(false);
  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const [filters, setFilters] = useState({
    status: "",
    role: "",
    search: "",
  });

  const { execute: fetchUsers } = useApiOperation("fetch users");
  const { execute: updateUserStatus } = useApiOperation("update user status");
  const { execute: deleteUser } = useApiOperation("delete user");

  useEffect(() => {
    loadUsers();
  }, []);

  const loadUsers = async () => {
    try {
      setLoading(true);
      const response = await fetchUsers(() => fetch("/api/users"));
      if (response.success) {
        setUsers(response.data);
      }
    } catch (error) {
      console.error("Error loading users:", error);
      toast.error("Failed to load users");
    } finally {
      setLoading(false);
    }
  };

  const handleStatusUpdate = async (userId, newStatus) => {
    try {
      const response = await updateUserStatus(() =>
        fetch(`/api/users/${userId}/status`, {
          method: "PUT",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ status: newStatus }),
        })
      );

      if (response.success) {
        toast.success("User status updated successfully");
        setUsers(
          users.map((user) =>
            user.id === userId ? { ...user, status: newStatus } : user
          )
        );
        setShowStatusModal(false);
        setSelectedUser(null);
      }
    } catch (error) {
      console.error("Error updating user status:", error);
      toast.error("Failed to update user status");
    }
  };

  const handleDeleteUser = async (userId) => {
    try {
      const response = await deleteUser(() =>
        fetch(`/api/users/${userId}`, {
          method: "DELETE",
        })
      );

      if (response.success) {
        toast.success("User deleted successfully");
        setUsers(users.filter((user) => user.id !== userId));
        setShowDeleteModal(false);
        setSelectedUser(null);
      }
    } catch (error) {
      console.error("Error deleting user:", error);
      toast.error("Failed to delete user");
    }
  };

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleDateString("en-US", {
      year: "numeric",
      month: "short",
      day: "numeric",
      hour: "2-digit",
      minute: "2-digit",
    });
  };

  const getStatusColor = (status) => {
    switch (status) {
      case "active":
        return "success";
      case "pending":
        return "warning";
      case "suspended":
        return "danger";
      case "inactive":
        return "secondary";
      default:
        return "secondary";
    }
  };

  const getRoleColor = (role) => {
    switch (role) {
      case "admin":
        return "danger";
      case "manager":
        return "warning";
      case "customer":
        return "success";
      default:
        return "secondary";
    }
  };

  const filteredUsers = users.filter((user) => {
    const matchesStatus = !filters.status || user.status === filters.status;
    const matchesRole = !filters.role || user.role === filters.role;
    const matchesSearch =
      !filters.search ||
      user.firstName?.toLowerCase().includes(filters.search.toLowerCase()) ||
      user.lastName?.toLowerCase().includes(filters.search.toLowerCase()) ||
      user.email?.toLowerCase().includes(filters.search.toLowerCase());

    return matchesStatus && matchesRole && matchesSearch;
  });

  if (loading) {
    return (
      <div className={styles.loadingContainer}>
        <div className={styles.loadingSpinner}></div>
        <p>Loading users...</p>
      </div>
    );
  }

  return (
    <div className={styles.container}>
      <div className={styles.header}>
        <h1>User Management</h1>
        <p>Manage all users in the system</p>
      </div>

      {/* Filters */}
      <Card className={styles.filtersCard}>
        <div className={styles.filtersContent}>
          <div className={styles.searchSection}>
            <input
              type="text"
              placeholder="Search users..."
              value={filters.search}
              onChange={(e) =>
                setFilters({ ...filters, search: e.target.value })
              }
              className={styles.searchInput}
            />
          </div>

          <div className={styles.filterSection}>
            <select
              value={filters.status}
              onChange={(e) =>
                setFilters({ ...filters, status: e.target.value })
              }
              className={styles.filterSelect}
            >
              <option value="">All Status</option>
              <option value="active">Active</option>
              <option value="pending">Pending</option>
              <option value="suspended">Suspended</option>
              <option value="inactive">Inactive</option>
            </select>

            <select
              value={filters.role}
              onChange={(e) => setFilters({ ...filters, role: e.target.value })}
              className={styles.filterSelect}
            >
              <option value="">All Roles</option>
              <option value="admin">Admin</option>
              <option value="manager">Manager</option>
              <option value="customer">Customer</option>
            </select>
          </div>
        </div>
      </Card>

      {/* Users List */}
      <div className={styles.usersGrid}>
        {filteredUsers.map((user) => (
          <Card key={user.id} className={styles.userCard}>
            <div className={styles.userHeader}>
              <div className={styles.userAvatar}>
                {user.firstName?.charAt(0)}
                {user.lastName?.charAt(0)}
              </div>
              <div className={styles.userInfo}>
                <h3>
                  {user.firstName} {user.lastName}
                </h3>
                <p>{user.email}</p>
                <div className={styles.userBadges}>
                  <span
                    className={`${styles.statusBadge} ${styles[getStatusColor(user.status)]}`}
                  >
                    {user.status}
                  </span>
                  <span
                    className={`${styles.roleBadge} ${styles[getRoleColor(user.role)]}`}
                  >
                    {user.role}
                  </span>
                </div>
              </div>
            </div>

            <div className={styles.userDetails}>
              <div className={styles.detailItem}>
                <span className={styles.detailLabel}>Created:</span>
                <span>{formatDate(user.createdAt)}</span>
              </div>
              {user.lastLoginAt && (
                <div className={styles.detailItem}>
                  <span className={styles.detailLabel}>Last Login:</span>
                  <span>{formatDate(user.lastLoginAt)}</span>
                </div>
              )}
            </div>

            <div className={styles.userActions}>
              <Button
                variant="outline"
                size="small"
                onClick={() => {
                  setSelectedUser(user);
                  setShowStatusModal(true);
                }}
              >
                Change Status
              </Button>

              {user.role !== "admin" && (
                <Button
                  variant="danger"
                  size="small"
                  onClick={() => {
                    setSelectedUser(user);
                    setShowDeleteModal(true);
                  }}
                >
                  Delete
                </Button>
              )}
            </div>
          </Card>
        ))}
      </div>

      {filteredUsers.length === 0 && (
        <Card className={styles.emptyCard}>
          <div className={styles.emptyState}>
            <i className="fas fa-users"></i>
            <h3>No users found</h3>
            <p>Try adjusting your filters or search terms</p>
          </div>
        </Card>
      )}

      {/* Status Update Modal */}
      {showStatusModal && selectedUser && (
        <div className={styles.modalOverlay}>
          <div className={styles.modal}>
            <div className={styles.modalHeader}>
              <h3>Update User Status</h3>
              <button
                onClick={() => {
                  setShowStatusModal(false);
                  setSelectedUser(null);
                }}
                className={styles.closeButton}
              >
                ×
              </button>
            </div>

            <div className={styles.modalContent}>
              <p>
                Update status for{" "}
                <strong>
                  {selectedUser.firstName} {selectedUser.lastName}
                </strong>
              </p>

              <div className={styles.statusOptions}>
                {["active", "pending", "suspended", "inactive"].map(
                  (status) => (
                    <Button
                      key={status}
                      variant={
                        selectedUser.status === status ? "primary" : "outline"
                      }
                      onClick={() =>
                        handleStatusUpdate(selectedUser.id, status)
                      }
                      className={styles.statusOption}
                    >
                      {status.charAt(0).toUpperCase() + status.slice(1)}
                    </Button>
                  )
                )}
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Delete Confirmation Modal */}
      {showDeleteModal && selectedUser && (
        <div className={styles.modalOverlay}>
          <div className={styles.modal}>
            <div className={styles.modalHeader}>
              <h3>Delete User</h3>
              <button
                onClick={() => {
                  setShowDeleteModal(false);
                  setSelectedUser(null);
                }}
                className={styles.closeButton}
              >
                ×
              </button>
            </div>

            <div className={styles.modalContent}>
              <div className={styles.warningMessage}>
                <i className="fas fa-exclamation-triangle"></i>
                <p>
                  Are you sure you want to delete{" "}
                  <strong>
                    {selectedUser.firstName} {selectedUser.lastName}
                  </strong>
                  ?
                </p>
                <p>This action cannot be undone.</p>
              </div>

              <div className={styles.modalActions}>
                <Button
                  variant="outline"
                  onClick={() => {
                    setShowDeleteModal(false);
                    setSelectedUser(null);
                  }}
                >
                  Cancel
                </Button>
                <Button
                  variant="danger"
                  onClick={() => handleDeleteUser(selectedUser.id)}
                >
                  Delete User
                </Button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default UserManagementPage;
