import React, { useState, useEffect } from 'react';
import { useApiOperation } from '../../hooks/useApiOperation';
import { Card, Button } from '../../components/common';
import { toast } from 'react-hot-toast';
import styles from './AdminDashboardPage.module.css';

const NotificationsPage = () => {
  const [notifications, setNotifications] = useState([]);
  const [loading, setLoading] = useState(true);

  const { execute: fetchNotifications } = useApiOperation('fetch notifications');

  useEffect(() => {
    const loadNotifications = async () => {
      try {
        setLoading(true);
        const response = await fetchNotifications(() => fetch('/api/notifications/admin/stats'));
        if (response.success) {
          setNotifications(response.data.recent || []);
        }
      } catch (error) {
        console.error('Error loading notifications:', error);
        toast.error('Failed to load notifications');
      } finally {
        setLoading(false);
      }
    };

    loadNotifications();
  }, [fetchNotifications]);

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  const getTypeColor = (type) => {
    switch (type) {
      case 'new_user_registration': return 'success';
      case 'loan_status': return 'warning';
      case 'status_change': return 'info';
      default: return 'secondary';
    }
  };

  if (loading) {
    return (
      <div className={styles.loadingContainer}>
        <div className={styles.loadingSpinner}></div>
        <p>Loading notifications...</p>
      </div>
    );
  }

  return (
    <div className={styles.container}>
      <div className={styles.header}>
        <h1>Notifications Management</h1>
        <p>Monitor and manage system notifications</p>
      </div>

      <div className={styles.summaryGrid}>
        <Card className={styles.summaryCard}>
          <div className={styles.summaryContent}>
            <div className={styles.summaryIcon}>
              <i className="fas fa-bell"></i>
            </div>
            <div className={styles.summaryInfo}>
              <h3>Total Notifications</h3>
              <p>All system notifications</p>
              <small>{notifications.length || 0} Total</small>
            </div>
          </div>
        </Card>

        <Card className={styles.summaryCard}>
          <div className={styles.summaryContent}>
            <div className={styles.summaryIcon}>
              <i className="fas fa-eye"></i>
            </div>
            <div className={styles.summaryInfo}>
              <h3>Unread</h3>
              <p>Pending notifications</p>
              <small>{notifications.filter(n => !n.isRead).length || 0} Unread</small>
            </div>
          </div>
        </Card>

        <Card className={styles.summaryCard}>
          <div className={styles.summaryContent}>
            <div className={styles.summaryIcon}>
              <i className="fas fa-users"></i>
            </div>
            <div className={styles.summaryInfo}>
              <h3>User Registrations</h3>
              <p>New user notifications</p>
              <small>{notifications.filter(n => n.type === 'new_user_registration').length || 0} Pending</small>
            </div>
          </div>
        </Card>

        <Card className={styles.summaryCard}>
          <div className={styles.summaryContent}>
            <div className={styles.summaryIcon}>
              <i className="fas fa-hand-holding-usd"></i>
            </div>
            <div className={styles.summaryInfo}>
              <h3>Loan Applications</h3>
              <p>Loan-related notifications</p>
              <small>{notifications.filter(n => n.type === 'loan_status').length || 0} Pending</small>
            </div>
          </div>
        </Card>
      </div>

      <Card className={styles.card}>
        <div className={styles.cardHeader}>
          <h2>Recent Notifications</h2>
          <Button variant="link" onClick={() => window.location.href = '/admin/users'}>
            View All Users
          </Button>
        </div>
        <div className={styles.userList}>
          {notifications.map((notification) => (
            <div key={notification.id} className={styles.userItem}>
              <div className={styles.userInfo}>
                <div className={styles.userAvatar}>
                  <i className={`fas fa-${notification.type === 'new_user_registration' ? 'user-plus' : 
                    notification.type === 'loan_status' ? 'hand-holding-usd' : 'bell'}`}></i>
                </div>
                <div className={styles.userDetails}>
                  <h4>{notification.title}</h4>
                  <p>{notification.message}</p>
                  <span className={`${styles.statusBadge} ${styles[getTypeColor(notification.type)]}`}>
                    {notification.type.replace(/_/g, ' ')}
                  </span>
                </div>
              </div>
              <div className={styles.userMeta}>
                <small>{formatDate(notification.createdAt)}</small>
                {!notification.isRead && (
                  <span className={styles.unreadBadge}>Unread</span>
                )}
              </div>
            </div>
          ))}
        </div>
      </Card>

      <div className={styles.quickActions}>
        <h2>Quick Actions</h2>
        <div className={styles.actionGrid}>
          <Button 
            variant="outline" 
            onClick={() => window.location.href = '/admin/users'}
            className={styles.actionButton}
          >
            <i className="fas fa-users"></i>
            Manage Users
          </Button>
          
          <Button 
            variant="outline" 
            onClick={() => window.location.href = '/admin/loans'}
            className={styles.actionButton}
          >
            <i className="fas fa-hand-holding-usd"></i>
            Review Loans
          </Button>
          
          <Button 
            variant="outline" 
            onClick={() => window.location.href = '/admin/reports'}
            className={styles.actionButton}
          >
            <i className="fas fa-chart-bar"></i>
            View Reports
          </Button>
          
          <Button 
            variant="outline" 
            onClick={() => window.location.href = '/admin/security'}
            className={styles.actionButton}
          >
            <i className="fas fa-shield-alt"></i>
            Security Settings
          </Button>
        </div>
      </div>
    </div>
  );
};

export default NotificationsPage;
