import React, { useState, useEffect } from 'react';
import { useApiOperation } from '../../hooks/useApiOperation';
import { Card, Button } from '../../components/common';
import { toast } from 'react-hot-toast';
import styles from './LoanApplicationsPage.module.css';

const LoanApplicationsPage = () => {
  const [loans, setLoans] = useState([]);
  const [loading, setLoading] = useState(true);
  const [selectedLoan, setSelectedLoan] = useState(null);
  const [showApprovalModal, setShowApprovalModal] = useState(false);
  const [showRejectionModal, setShowRejectionModal] = useState(false);
  const [filters, setFilters] = useState({
    status: '',
    search: ''
  });

  const { execute: fetchLoans } = useApiOperation('fetch loans');
  const { execute: updateLoanStatus } = useApiOperation('update loan status');

  useEffect(() => {
    loadLoans();
  }, []);

  const loadLoans = async () => {
    try {
      setLoading(true);
      const response = await fetchLoans(() => fetch('/api/loans'));
      if (response.success) {
        setLoans(response.data);
      }
    } catch (error) {
      console.error('Error loading loans:', error);
      toast.error('Failed to load loan applications');
    } finally {
      setLoading(false);
    }
  };

  const handleLoanAction = async (loanId, action, reason = '') => {
    try {
      const response = await updateLoanStatus(() => 
        fetch(`/api/loans/${loanId}/status`, {
          method: 'PUT',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({ 
            status: action === 'approve' ? 'approved' : 'rejected',
            reason: reason
          })
        })
      );

      if (response.success) {
        toast.success(`Loan ${action}d successfully`);
        setLoans(loans.map(loan => 
          loan.id === loanId ? { ...loan, status: action === 'approve' ? 'approved' : 'rejected' } : loan
        ));
        setShowApprovalModal(false);
        setShowRejectionModal(false);
        setSelectedLoan(null);
      }
    } catch (error) {
      console.error('Error updating loan status:', error);
      toast.error(`Failed to ${action} loan`);
    }
  };

  const formatCurrency = (amount) => {
    return new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: 'USD'
    }).format(amount || 0);
  };

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  const getStatusColor = (status) => {
    switch (status) {
      case 'approved': return 'success';
      case 'pending': return 'warning';
      case 'rejected': return 'danger';
      default: return 'secondary';
    }
  };

  const getLoanTypeColor = (type) => {
    switch (type) {
      case 'personal': return 'primary';
      case 'business': return 'warning';
      case 'mortgage': return 'success';
      case 'auto': return 'info';
      default: return 'secondary';
    }
  };

  const filteredLoans = loans.filter(loan => {
    const matchesStatus = !filters.status || loan.status === filters.status;
    const matchesSearch = !filters.search || 
      loan.userName?.toLowerCase().includes(filters.search.toLowerCase()) ||
      loan.loanType?.toLowerCase().includes(filters.search.toLowerCase()) ||
      loan.purpose?.toLowerCase().includes(filters.search.toLowerCase());
    
    return matchesStatus && matchesSearch;
  });

  if (loading) {
    return (
      <div className={styles.loadingContainer}>
        <div className={styles.loadingSpinner}></div>
        <p>Loading loan applications...</p>
      </div>
    );
  }

  return (
    <div className={styles.container}>
      <div className={styles.header}>
        <h1>Loan Applications</h1>
        <p>Review and manage loan applications</p>
      </div>

      {/* Filters */}
      <Card className={styles.filtersCard}>
        <div className={styles.filtersContent}>
          <div className={styles.searchSection}>
            <input
              type="text"
              placeholder="Search loans..."
              value={filters.search}
              onChange={(e) => setFilters({ ...filters, search: e.target.value })}
              className={styles.searchInput}
            />
          </div>
          
          <div className={styles.filterSection}>
            <select
              value={filters.status}
              onChange={(e) => setFilters({ ...filters, status: e.target.value })}
              className={styles.filterSelect}
            >
              <option value="">All Status</option>
              <option value="pending">Pending</option>
              <option value="approved">Approved</option>
              <option value="rejected">Rejected</option>
            </select>
          </div>
        </div>
      </Card>

      {/* Loans List */}
      <div className={styles.loansGrid}>
        {filteredLoans.map((loan) => (
          <Card key={loan.id} className={styles.loanCard}>
            <div className={styles.loanHeader}>
              <div className={styles.loanInfo}>
                <h3>{loan.userName}</h3>
                <p>{loan.email}</p>
                <div className={styles.loanBadges}>
                  <span className={`${styles.statusBadge} ${styles[getStatusColor(loan.status)]}`}>
                    {loan.status}
                  </span>
                  <span className={`${styles.typeBadge} ${styles[getLoanTypeColor(loan.loanType)]}`}>
                    {loan.loanType}
                  </span>
                </div>
              </div>
              <div className={styles.loanAmount}>
                <h2>{formatCurrency(loan.amount)}</h2>
                <small>{loan.interestRate}% APR</small>
              </div>
            </div>
            
            <div className={styles.loanDetails}>
              <div className={styles.detailItem}>
                <span className={styles.detailLabel}>Purpose:</span>
                <span>{loan.purpose}</span>
              </div>
              <div className={styles.detailItem}>
                <span className={styles.detailLabel}>Term:</span>
                <span>{loan.term} months</span>
              </div>
              <div className={styles.detailItem}>
                <span className={styles.detailLabel}>Applied:</span>
                <span>{formatDate(loan.createdAt)}</span>
              </div>
              {loan.income && (
                <div className={styles.detailItem}>
                  <span className={styles.detailLabel}>Income:</span>
                  <span>{formatCurrency(loan.income)}</span>
                </div>
              )}
            </div>
            
            {loan.status === 'pending' && (
              <div className={styles.loanActions}>
                <Button
                  variant="success"
                  size="small"
                  onClick={() => {
                    setSelectedLoan(loan);
                    setShowApprovalModal(true);
                  }}
                >
                  <i className="fas fa-check"></i>
                  Approve
                </Button>
                
                <Button
                  variant="danger"
                  size="small"
                  onClick={() => {
                    setSelectedLoan(loan);
                    setShowRejectionModal(true);
                  }}
                >
                  <i className="fas fa-times"></i>
                  Reject
                </Button>
              </div>
            )}
          </Card>
        ))}
      </div>

      {filteredLoans.length === 0 && (
        <Card className={styles.emptyCard}>
          <div className={styles.emptyState}>
            <i className="fas fa-hand-holding-usd"></i>
            <h3>No loan applications found</h3>
            <p>Try adjusting your filters or search terms</p>
          </div>
        </Card>
      )}

      {/* Approval Modal */}
      {showApprovalModal && selectedLoan && (
        <div className={styles.modalOverlay}>
          <div className={styles.modal}>
            <div className={styles.modalHeader}>
              <h3>Approve Loan Application</h3>
              <button
                onClick={() => {
                  setShowApprovalModal(false);
                  setSelectedLoan(null);
                }}
                className={styles.closeButton}
              >
                ×
              </button>
            </div>
            
            <div className={styles.modalContent}>
              <div className={styles.approvalInfo}>
                <h4>Loan Details</h4>
                <p><strong>Applicant:</strong> {selectedLoan.userName}</p>
                <p><strong>Amount:</strong> {formatCurrency(selectedLoan.amount)}</p>
                <p><strong>Type:</strong> {selectedLoan.loanType}</p>
                <p><strong>Purpose:</strong> {selectedLoan.purpose}</p>
              </div>
              
              <div className={styles.modalActions}>
                <Button
                  variant="outline"
                  onClick={() => {
                    setShowApprovalModal(false);
                    setSelectedLoan(null);
                  }}
                >
                  Cancel
                </Button>
                <Button
                  variant="success"
                  onClick={() => handleLoanAction(selectedLoan.id, 'approve')}
                >
                  Approve Loan
                </Button>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Rejection Modal */}
      {showRejectionModal && selectedLoan && (
        <div className={styles.modalOverlay}>
          <div className={styles.modal}>
            <div className={styles.modalHeader}>
              <h3>Reject Loan Application</h3>
              <button
                onClick={() => {
                  setShowRejectionModal(false);
                  setSelectedLoan(null);
                }}
                className={styles.closeButton}
              >
                ×
              </button>
            </div>
            
            <div className={styles.modalContent}>
              <div className={styles.rejectionInfo}>
                <h4>Loan Details</h4>
                <p><strong>Applicant:</strong> {selectedLoan.userName}</p>
                <p><strong>Amount:</strong> {formatCurrency(selectedLoan.amount)}</p>
                <p><strong>Type:</strong> {selectedLoan.loanType}</p>
                <p><strong>Purpose:</strong> {selectedLoan.purpose}</p>
              </div>
              
              <div className={styles.reasonSection}>
                <label htmlFor="rejectionReason">Reason for rejection (optional):</label>
                <textarea
                  id="rejectionReason"
                  placeholder="Enter reason for rejection..."
                  className={styles.reasonTextarea}
                  rows="3"
                />
              </div>
              
              <div className={styles.modalActions}>
                <Button
                  variant="outline"
                  onClick={() => {
                    setShowRejectionModal(false);
                    setSelectedLoan(null);
                  }}
                >
                  Cancel
                </Button>
                <Button
                  variant="danger"
                  onClick={() => {
                    const reason = document.getElementById('rejectionReason').value;
                    handleLoanAction(selectedLoan.id, 'reject', reason);
                  }}
                >
                  Reject Loan
                </Button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default LoanApplicationsPage; 