import React, { useState, useEffect } from "react";
import { useAuth } from "../../context/AuthContext";
import { useApiOperation } from "../../hooks/useApiOperation";
import { Card, Button } from "../../components/common";
import { toast } from "react-hot-toast";
import styles from "./AdminDashboardPage.module.css";

const AdminDashboardPage = () => {
  const { user } = useAuth();
  const [financialSummary, setFinancialSummary] = useState(null);
  const [notificationStats, setNotificationStats] = useState(null);
  const [recentUsers, setRecentUsers] = useState([]);
  const [recentTransactions, setRecentTransactions] = useState([]);
  const [loading, setLoading] = useState(true);

  const { execute: fetchFinancialSummary } = useApiOperation(
    "fetch financial summary"
  );
  const { execute: fetchNotificationStats } = useApiOperation(
    "fetch notification stats"
  );
  const { execute: fetchUsers } = useApiOperation("fetch users");
  const { execute: fetchTransactions } = useApiOperation("fetch transactions");

  useEffect(() => {
    const loadDashboardData = async () => {
      try {
        setLoading(true);

        // Fetch all dashboard data in parallel
        const [financialData, notificationData, usersData, transactionsData] =
          await Promise.all([
            fetchFinancialSummary(() =>
              fetch("/api/reports/financial-summary")
            ),
            fetchNotificationStats(() =>
              fetch("/api/notifications/admin/stats")
            ),
            fetchUsers(() => fetch("/api/users")),
            fetchTransactions(() =>
              fetch("/api/transactions/admin/all?limit=5")
            ),
          ]);

        if (financialData.success) setFinancialSummary(financialData.data);
        if (notificationData.success)
          setNotificationStats(notificationData.data);
        if (usersData.success) setRecentUsers(usersData.data.slice(0, 5));
        if (transactionsData.success)
          setRecentTransactions(transactionsData.data.slice(0, 5));
      } catch (error) {
        console.error("Error loading dashboard data:", error);
        toast.error("Failed to load dashboard data");
      } finally {
        setLoading(false);
      }
    };

    loadDashboardData();
  }, [
    fetchFinancialSummary,
    fetchNotificationStats,
    fetchUsers,
    fetchTransactions,
  ]);

  const formatCurrency = (amount) => {
    return new Intl.NumberFormat("en-US", {
      style: "currency",
      currency: "USD",
    }).format(amount || 0);
  };

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleDateString("en-US", {
      year: "numeric",
      month: "short",
      day: "numeric",
      hour: "2-digit",
      minute: "2-digit",
    });
  };

  const getStatusColor = (status) => {
    switch (status) {
      case "active":
        return "success";
      case "pending":
        return "warning";
      case "suspended":
        return "danger";
      case "inactive":
        return "secondary";
      default:
        return "secondary";
    }
  };

  if (loading) {
    return (
      <div className={styles.loadingContainer}>
        <div className={styles.loadingSpinner}></div>
        <p>Loading admin dashboard...</p>
      </div>
    );
  }

  return (
    <div className={styles.container}>
      <div className={styles.header}>
        <h1>Admin Dashboard</h1>
        <p>
          Welcome back, {user?.firstName} {user?.lastName}
        </p>
      </div>

      {/* Financial Summary Cards */}
      <div className={styles.summaryGrid}>
        <Card className={styles.summaryCard}>
          <div className={styles.summaryContent}>
            <div className={styles.summaryIcon}>
              <i className="fas fa-users"></i>
            </div>
            <div className={styles.summaryInfo}>
              <h3>{financialSummary?.users?.totalUsers || 0}</h3>
              <p>Total Users</p>
              <small>{financialSummary?.users?.activeUsers || 0} Active</small>
            </div>
          </div>
        </Card>

        <Card className={styles.summaryCard}>
          <div className={styles.summaryContent}>
            <div className={styles.summaryIcon}>
              <i className="fas fa-wallet"></i>
            </div>
            <div className={styles.summaryInfo}>
              <h3>{financialSummary?.accounts?.totalAccounts || 0}</h3>
              <p>Total Accounts</p>
              <small>
                {formatCurrency(financialSummary?.accounts?.totalBalance || 0)}
              </small>
            </div>
          </div>
        </Card>

        <Card className={styles.summaryCard}>
          <div className={styles.summaryContent}>
            <div className={styles.summaryIcon}>
              <i className="fas fa-exchange-alt"></i>
            </div>
            <div className={styles.summaryInfo}>
              <h3>{financialSummary?.transactions?.totalTransactions || 0}</h3>
              <p>Total Transactions</p>
              <small>
                {formatCurrency(
                  financialSummary?.transactions?.totalDeposits || 0
                )}{" "}
                Deposits
              </small>
            </div>
          </div>
        </Card>

        <Card className={styles.summaryCard}>
          <div className={styles.summaryContent}>
            <div className={styles.summaryIcon}>
              <i className="fas fa-bell"></i>
            </div>
            <div className={styles.summaryInfo}>
              <h3>{notificationStats?.total || 0}</h3>
              <p>Notifications</p>
              <small>{notificationStats?.unread || 0} Unread</small>
            </div>
          </div>
        </Card>
      </div>

      {/* Main Content Grid */}
      <div className={styles.mainGrid}>
        {/* Recent Users */}
        <Card className={styles.card}>
          <div className={styles.cardHeader}>
            <h2>Recent Users</h2>
            <Button
              variant="link"
              onClick={() => (window.location.href = "/admin/users")}
            >
              View All
            </Button>
          </div>
          <div className={styles.userList}>
            {recentUsers.map((user) => (
              <div key={user.id} className={styles.userItem}>
                <div className={styles.userInfo}>
                  <div className={styles.userAvatar}>
                    {user.firstName?.charAt(0)}
                    {user.lastName?.charAt(0)}
                  </div>
                  <div className={styles.userDetails}>
                    <h4>
                      {user.firstName} {user.lastName}
                    </h4>
                    <p>{user.email}</p>
                    <span
                      className={`${styles.statusBadge} ${styles[getStatusColor(user.status)]}`}
                    >
                      {user.status}
                    </span>
                  </div>
                </div>
                <div className={styles.userMeta}>
                  <small>{formatDate(user.createdAt)}</small>
                </div>
              </div>
            ))}
          </div>
        </Card>

        {/* Recent Transactions */}
        <Card className={styles.card}>
          <div className={styles.cardHeader}>
            <h2>Recent Transactions</h2>
            <Button
              variant="link"
              onClick={() => (window.location.href = "/admin/transactions")}
            >
              View All
            </Button>
          </div>
          <div className={styles.transactionList}>
            {recentTransactions.map((transaction) => (
              <div key={transaction.id} className={styles.transactionItem}>
                <div className={styles.transactionInfo}>
                  <div className={styles.transactionIcon}>
                    <i
                      className={`fas fa-${transaction.amount > 0 ? "arrow-down" : "arrow-up"}`}
                    ></i>
                  </div>
                  <div className={styles.transactionDetails}>
                    <h4>{transaction.description}</h4>
                    <p>{transaction.accountNumber}</p>
                    <span
                      className={`${styles.statusBadge} ${styles[getStatusColor(transaction.status)]}`}
                    >
                      {transaction.status}
                    </span>
                  </div>
                </div>
                <div className={styles.transactionAmount}>
                  <span
                    className={
                      transaction.amount > 0 ? styles.positive : styles.negative
                    }
                  >
                    {formatCurrency(Math.abs(transaction.amount))}
                  </span>
                  <small>{formatDate(transaction.createdAt)}</small>
                </div>
              </div>
            ))}
          </div>
        </Card>
      </div>

      {/* Quick Actions */}
      <div className={styles.quickActions}>
        <h2>Quick Actions</h2>
        <div className={styles.actionGrid}>
          <Button
            variant="outline"
            onClick={() => (window.location.href = "/admin/users")}
            className={styles.actionButton}
          >
            <i className="fas fa-users"></i>
            Manage Users
          </Button>

          <Button
            variant="outline"
            onClick={() => (window.location.href = "/admin/notifications")}
            className={styles.actionButton}
          >
            <i className="fas fa-bell"></i>
            Notifications
          </Button>

          <Button
            variant="outline"
            onClick={() => (window.location.href = "/admin/reports")}
            className={styles.actionButton}
          >
            <i className="fas fa-chart-bar"></i>
            Reports
          </Button>

          <Button
            variant="outline"
            onClick={() => (window.location.href = "/admin/loans")}
            className={styles.actionButton}
          >
            <i className="fas fa-hand-holding-usd"></i>
            Loan Applications
          </Button>
        </div>
      </div>
    </div>
  );
};

export default AdminDashboardPage;
