import React, { useState, useEffect } from 'react';
import { useApiOperation } from '../../hooks/useApiOperation';
import { Card, Button } from '../../components/common';
import { toast } from 'react-hot-toast';
import styles from './ReportsPage.module.css';

const ReportsPage = () => {
  const [financialSummary, setFinancialSummary] = useState(null);
  const [transactionReport, setTransactionReport] = useState([]);
  const [loading, setLoading] = useState(true);
  const [dateRange, setDateRange] = useState({
    startDate: new Date(Date.now() - 30 * 24 * 60 * 60 * 1000).toISOString().split('T')[0],
    endDate: new Date().toISOString().split('T')[0]
  });

  const { execute: fetchFinancialSummary } = useApiOperation('fetch financial summary');
  const { execute: fetchTransactionReport } = useApiOperation('fetch transaction report');

  useEffect(() => {
    loadReports();
  }, [dateRange]);

  const loadReports = async () => {
    try {
      setLoading(true);
      
      const [financialData, transactionData] = await Promise.all([
        fetchFinancialSummary(() => fetch('/api/reports/financial-summary')),
        fetchTransactionReport(() => 
          fetch(`/api/reports/transactions?startDate=${dateRange.startDate}&endDate=${dateRange.endDate}`)
        )
      ]);

      if (financialData.success) setFinancialSummary(financialData.data);
      if (transactionData.success) setTransactionReport(transactionData.data);
      
    } catch (error) {
      console.error('Error loading reports:', error);
      toast.error('Failed to load reports');
    } finally {
      setLoading(false);
    }
  };

  const formatCurrency = (amount) => {
    return new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: 'USD'
    }).format(amount || 0);
  };

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  const getStatusColor = (status) => {
    switch (status) {
      case 'completed': return 'success';
      case 'pending': return 'warning';
      case 'failed': return 'danger';
      default: return 'secondary';
    }
  };

  const exportToCSV = (data, filename) => {
    const csvContent = "data:text/csv;charset=utf-8," + 
      data.map(row => Object.values(row).join(",")).join("\n");
    const encodedUri = encodeURI(csvContent);
    const link = document.createElement("a");
    link.setAttribute("href", encodedUri);
    link.setAttribute("download", `${filename}.csv`);
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  if (loading) {
    return (
      <div className={styles.loadingContainer}>
        <div className={styles.loadingSpinner}></div>
        <p>Loading reports...</p>
      </div>
    );
  }

  return (
    <div className={styles.container}>
      <div className={styles.header}>
        <h1>Financial Reports</h1>
        <p>Comprehensive financial analysis and reporting</p>
      </div>

      {/* Date Range Filter */}
      <Card className={styles.filterCard}>
        <div className={styles.filterContent}>
          <div className={styles.dateInputs}>
            <div className={styles.dateInput}>
              <label>Start Date:</label>
              <input
                type="date"
                value={dateRange.startDate}
                onChange={(e) => setDateRange({ ...dateRange, startDate: e.target.value })}
                className={styles.datePicker}
              />
            </div>
            <div className={styles.dateInput}>
              <label>End Date:</label>
              <input
                type="date"
                value={dateRange.endDate}
                onChange={(e) => setDateRange({ ...dateRange, endDate: e.target.value })}
                className={styles.datePicker}
              />
            </div>
          </div>
          <Button onClick={loadReports} className={styles.refreshButton}>
            <i className="fas fa-sync-alt"></i>
            Refresh
          </Button>
        </div>
      </Card>

      {/* Financial Summary Cards */}
      <div className={styles.summaryGrid}>
        <Card className={styles.summaryCard}>
          <div className={styles.summaryContent}>
            <div className={styles.summaryIcon}>
              <i className="fas fa-users"></i>
            </div>
            <div className={styles.summaryInfo}>
              <h3>{financialSummary?.users?.totalUsers || 0}</h3>
              <p>Total Users</p>
              <div className={styles.summaryBreakdown}>
                <small>{financialSummary?.users?.customers || 0} Customers</small>
                <small>{financialSummary?.users?.managers || 0} Managers</small>
                <small>{financialSummary?.users?.admins || 0} Admins</small>
              </div>
            </div>
          </div>
        </Card>

        <Card className={styles.summaryCard}>
          <div className={styles.summaryContent}>
            <div className={styles.summaryIcon}>
              <i className="fas fa-wallet"></i>
            </div>
            <div className={styles.summaryInfo}>
              <h3>{financialSummary?.accounts?.totalAccounts || 0}</h3>
              <p>Total Accounts</p>
              <small>{formatCurrency(financialSummary?.accounts?.totalBalance || 0)}</small>
            </div>
          </div>
        </Card>

        <Card className={styles.summaryCard}>
          <div className={styles.summaryContent}>
            <div className={styles.summaryIcon}>
              <i className="fas fa-exchange-alt"></i>
            </div>
            <div className={styles.summaryInfo}>
              <h3>{financialSummary?.transactions?.totalTransactions || 0}</h3>
              <p>Total Transactions</p>
              <div className={styles.summaryBreakdown}>
                <small>{formatCurrency(financialSummary?.transactions?.totalDeposits || 0)} Deposits</small>
                <small>{formatCurrency(financialSummary?.transactions?.totalWithdrawals || 0)} Withdrawals</small>
              </div>
            </div>
          </div>
        </Card>

        <Card className={styles.summaryCard}>
          <div className={styles.summaryContent}>
            <div className={styles.summaryIcon}>
              <i className="fas fa-hand-holding-usd"></i>
            </div>
            <div className={styles.summaryInfo}>
              <h3>{financialSummary?.loans?.totalLoans || 0}</h3>
              <p>Total Loans</p>
              <small>{formatCurrency(financialSummary?.loans?.totalLoanAmount || 0)}</small>
            </div>
          </div>
        </Card>
      </div>

      {/* Transaction Report */}
      <Card className={styles.reportCard}>
        <div className={styles.reportHeader}>
          <h2>Transaction Report</h2>
          <div className={styles.reportActions}>
            <Button 
              variant="outline" 
              onClick={() => exportToCSV(transactionReport, 'transaction-report')}
              className={styles.exportButton}
            >
              <i className="fas fa-download"></i>
              Export CSV
            </Button>
          </div>
        </div>
        
        <div className={styles.tableContainer}>
          <table className={styles.reportTable}>
            <thead>
              <tr>
                <th>Date</th>
                <th>Type</th>
                <th>Description</th>
                <th>Account</th>
                <th>User</th>
                <th>Amount</th>
                <th>Status</th>
              </tr>
            </thead>
            <tbody>
              {transactionReport.map((transaction) => (
                <tr key={transaction.id}>
                  <td>{formatDate(transaction.createdAt)}</td>
                  <td>
                    <span className={styles.transactionType}>
                      {transaction.transactionType}
                    </span>
                  </td>
                  <td>{transaction.description}</td>
                  <td>{transaction.accountNumber}</td>
                  <td>{transaction.firstName} {transaction.lastName}</td>
                  <td className={transaction.amount > 0 ? styles.positive : styles.negative}>
                    {formatCurrency(Math.abs(transaction.amount))}
                  </td>
                  <td>
                    <span className={`${styles.statusBadge} ${styles[getStatusColor(transaction.status)]}`}>
                      {transaction.status}
                    </span>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>

        {transactionReport.length === 0 && (
          <div className={styles.emptyState}>
            <i className="fas fa-chart-line"></i>
            <h3>No transactions found</h3>
            <p>No transactions match the selected date range</p>
          </div>
        )}
      </Card>

      {/* Additional Reports Section */}
      <div className={styles.additionalReports}>
        <h2>Additional Reports</h2>
        <div className={styles.reportsGrid}>
          <Card className={styles.reportOption}>
            <div className={styles.reportOptionContent}>
              <i className="fas fa-chart-pie"></i>
              <h3>User Activity Report</h3>
              <p>Track user login patterns and activity levels</p>
              <Button variant="outline">Generate Report</Button>
            </div>
          </Card>

          <Card className={styles.reportOption}>
            <div className={styles.reportOptionContent}>
              <i className="fas fa-chart-bar"></i>
              <h3>Revenue Analysis</h3>
              <p>Detailed revenue breakdown by period and category</p>
              <Button variant="outline">Generate Report</Button>
            </div>
          </Card>

          <Card className={styles.reportOption}>
            <div className={styles.reportOptionContent}>
              <i className="fas fa-shield-alt"></i>
              <h3>Security Report</h3>
              <p>Security incidents and compliance metrics</p>
              <Button variant="outline">Generate Report</Button>
            </div>
          </Card>
        </div>
      </div>
    </div>
  );
};

export default ReportsPage; 